// authentiq-js.js
// ------------------

// script strict mode syntax
'use strict';

require('./polyfills.js');
var radio = require('radio');
var Utils = require('./utils');
var AuthentiqAPI = require('./api');
var AuthentiqButton = require('./button');
var AuthentiqProvider = require('./provider');
var AuthentiqToken = require('./token');
var AuthentiqSession = require('./session');

if (DEBUG) {
  var $log = Utils.log;
}

(function(aq, undefined) {

  $log('timeEnd', '_loaded');

  $log('time', 'execution');
  $log('time', 'initFrame');
  $log('time', 'renderFrame');
  $log('time', 'rpFrame');
  $log('time', 'opFrame');

  aq._defaults = {
    _storage: {
      state: 'authentiq.request_state',
      nonce: 'authentiq.request_nonce',
      id_token: 'authentiq.id_token',
      session_state: 'authentiq.session_state'
    },

    direct_events: false,

    provider: {
      provider_uri: 'https://connect.authentiq.io',
      authorize_path: '/authorize',
      end_session_path: '/authorize/logout',
      check_session_iframe: '/authorize/iframe',

      // OAuth2
      client_id: '',

      // used in order RP can use snippet even without an id_token
      // RP server can then add this option to `true/yes/1`
      signed_in: null
    },

    button: {
      // OAuth2
      response_type: 'id_token',
      response_mode: 'fragment',
      redirect_uri: '',
      scope: '',              // if empty then let the OP use its default,
                              // if set always append openid
      state: '',

      // OIDC
      nonce: '',
      display: 'modal',
      prompt: '',
      ui_locales: '',

      // UI
      display_errors: '',     // client|provider, provider is the default value in OP
      sign_in_text: 'Sign in',
      sign_out_text: 'Sign out',
      sign_out_claim: 'first_name name email phone_number',

      theme: 'basic'          // basic|orange|grey|white
    }
  };

  // NOTE: Give a try to lodash
  // https://lodash.com/docs#defaultsDeep

  // extend user defaults with authentiq module defaults
  if ('defaults' in aq) {
    if (typeof aq.defaults.direct_events !== 'undefined') {
      aq._defaults.direct_events = true;
    }

    // this exists only for BDD tests so as tests does not mess up with snippets localStorage
    if (BDD_TESTS) {
      aq._defaults._storage = Object.assign({}, aq._defaults._storage, aq.defaults._storage);
    }

    aq._defaults.provider = Object.assign({}, aq._defaults.provider, aq.defaults.provider);
    aq._defaults.button = Object.assign({}, aq._defaults.button, aq.defaults.button);

    // delete the user defaults as it was being merged
    delete aq.defaults;
  }

  // load js script tag options from data-attrs
  var snippet = document.querySelector('.authentiq-snippet'),
      attr, attr_name, keys, key;

  if (snippet) {
    // -- Parse provider settings from script tag data attrs
    // read only the public options
    keys = ['provider', 'button'];
    for (key in keys) {
      for (attr in aq._defaults[keys[key]]) {
        attr_name = 'data-' + attr.split('_').join('-');
        if (snippet.hasAttribute(attr_name)) {
          aq._defaults[keys[key]][attr] = snippet.getAttribute(attr_name);
        }
      }
    }

    // check if direct_events are set
    if (snippet.hasAttribute('data-direct-events')) {
      aq._defaults.direct_events = true;
    }
  }

  // remove 'provider_uri' trailing slash
  if(aq._defaults.provider.provider_uri.substr(-1) === '/') {
    aq._defaults.provider.provider_uri = aq._defaults.provider.provider_uri.slice(0, -1);
  }

  // add 'authorize_path' and 'end_session_path' facing slash
  keys = ['authorize_path', 'end_session_path', 'check_session_iframe'];
  for (key in keys) {
    if(aq._defaults.provider[keys[key]][0] !== '/') {
      aq._defaults.provider[keys[key]] = '/' + aq._defaults.provider[keys[key]];
    }
  }

  // used in order RP can use snippet even without an id_token
  // accepts `true/yes/1` values for signed_in option
  aq._defaults.provider.signed_in = /(true|yes|1)/i.test(aq._defaults.provider.signed_in);

  // expose classes publicly
  aq.Event = function(name) {
    return radio('aq:' + name);
  };

  // add handy function so as devs can subscribe to our events.
  aq.subscribe = function(name, callback) {
    aq.Event(name).subscribe(callback);
  };

  aq.Token = new AuthentiqToken();
  aq.Provider = new AuthentiqProvider();

  // handle closing of popup
  if (window.opener) {
    // we need this `try` in case the main window has been opened from another page
    // otherwise JS will throw an error stopping the execution
    try {
      if (window.opener.authentiq && window.opener.authentiq.handlePopupCallback) {
        window.opener.authentiq.handlePopupCallback( Utils.getEventHistory() );
      } else {
        $log("Parent has no authentiq.handlePopupCallback() function to call.");
      }

      // close this current window
      try {
        window.close();
      } catch (e) {}

      // iOS bug won't let us close a popup if still loading
      Utils.on(window, 'load', function() {
        window.close();
      });

      return;

    } catch (e) {}
  }

  // handle closing of modal
  if (window.parent && window.parent.authentiq && window.parent.authentiq.handleModalCallback)
  {
    // we need this `try` in case the main window has been opened from another page
    // otherwise JS will throw an error stopping the execution
    try {
      window.parent.authentiq.handleModalCallback( Utils.getEventHistory() );
      return;
    } catch (e) {}
  }

  aq.popup_window = undefined;
  aq.handlePopupCallback = undefined;
  aq.handleModalCallback = undefined;

  aq.API = AuthentiqAPI;
  aq._API = new AuthentiqAPI();
  aq.signIn = aq._API.signIn.bind(aq._API);
  aq.signOut = aq.Provider.signOut;

  aq.Button = AuthentiqButton;
  aq.Session = new AuthentiqSession();

  $log('timeEnd', '_warmup');
  $log('Logged in: ', aq.Provider.isSignedIn());

  // bind buttons on DOMReady event
  // this will not be called when script loads in async mode
  Utils.on(document, 'DOMContentLoaded', function() {
    aq.init();
  });

  aq.init = function(el, opts) {
    // initiate custom element
    if (typeof el !== 'undefined') {
      new AuthentiqButton(el, opts);

    // or bootstrap existing elements
    } else {
      var buttons = document.querySelectorAll('.authentiq-button');

      // generate button for script tag
      if (snippet && /(true|yes|1)/i.test((snippet.getAttribute('data-button') || '').toLowerCase())) {
        new AuthentiqButton(snippet, opts);
      }

      // generate buttons for DOM elements with class .authentiq-button
      for (var i = 0, len = buttons.length; i < len; i++) {
        new AuthentiqButton(buttons[i], opts);
      }
    }

    // fire all events that happened before DOMContentLoaded
    Utils.fireEventsFromHistory();

    $log('timeEnd', 'execution');
    $log('timeEnd', '_execution');
  };

  module.exports = aq;

})(window.authentiq = window.authentiq || {});
