// provider.js
// ------------------

// script strict mode syntax
'use strict';

// private vars and functions
var queryString = require('query-string');
var Utils = require('./utils');

// Shorthand
var $aq;
var $Token;
var $Event;
var $storage_defaults;

if (DEBUG) {
  var $log = Utils.log;
}

// private vars
var _profile;
var _profile_exp;   // we keep in memory the id_token.exp as it is being removed from profile

var checkToken = function() {
  var token, state, nonce, profile,
      search_in_locations = ['hash', 'search'];

  for (var loc in search_in_locations) {

    if (location[ search_in_locations[loc] ]) {
      token = queryString.parse(location[ search_in_locations[loc] ]) || {};

      // check if OP returned an error
      if ('error' in token)
      {
        Utils.addEventInHistory('error', {
          error: token.error,
          error_description: token.error_description || null
        });

      // check if it is a valid token for id_token
      } else if ('state' in token &&
        ('id_token' in token || 'access_token' in token || 'code' in token))
      {

        // get stored state send in OP
        // and validate it with the response state
        state = localStorage.getItem($storage_defaults.state);

        if(token.state === state) {

          // store session_state which needed for Session check (opframe)
          $Token.setSessionState(token.session_state);

          if ('id_token' in token) {
            // get stored nonce send in OP
            // and validate it with the id_token nonce
            nonce = localStorage.getItem($storage_defaults.nonce);
            profile = decodeJWT(token.id_token);

            // remove strict validation of nonce for now
            if (typeof profile !== 'undefined') // &&
              // 'nonce' in profile && profile.nonce == nonce)
            {
              $Token.setIDToken(token.id_token);

              // publish event
              Utils.addEventInHistory('authorized:id_token', token.id_token);

              if (typeof profile.nonce === 'undefined' || profile.nonce != nonce) {
                Utils.addEventInHistory('error', {
                  error: 'client_error',
                  error_description: 'Invalid nonce in ID Token.'
                });
              }
            }
          }

          if ('token_type' in token && 'access_token' in token) {
            // TODO: validate access_token crypto hash left part === id_token.at_hash

            // publish event
            Utils.addEventInHistory('authorized:access_token', {
              token_type: token.token_type,
              access_token: token.access_token,
              expires_in: parseInt(token.expires_in, 0) || 0
            });
          }

          if ('code' in token) {
            // publish event
            Utils.addEventInHistory('authorized:code', token.code);
          }

          // publish event
          Utils.addEventInHistory('authorized', token);

        } else {
          Utils.addEventInHistory('error', {
            error: 'client_error',
            error_description: 'Invalid state in authorization response.'
          });
        }

        // delete the state, nonce cookie as it is not needed anymore
        localStorage.removeItem($storage_defaults.state);
        localStorage.removeItem($storage_defaults.nonce);

        // replace location hash as it is not needed for the user
        // when token found in fragments
        if (search_in_locations[loc] == 'hash') {
          removeHash();
        }

        break;
      }
    }
  }
};

var removeHash = function() {
  var scrollV, scrollH, loc = window.location;
  if ('pushState' in history) {
      history.replaceState('', document.title, loc.pathname + loc.search);

  } else {
      // Prevent scrolling by storing the page's current scroll offset
      scrollV = document.body.scrollTop;
      scrollH = document.body.scrollLeft;

      loc.hash = '';

      // Restore the scroll offset, should be flicker free
      document.body.scrollTop = scrollV;
      document.body.scrollLeft = scrollH;
  }
};

var decodeJWT = function(token) {
  var decodedJWT = token ? Utils.decodeJWT(token) : undefined;
  return decodedJWT ? decodedJWT.payload : undefined;
};

var loadProfile = function() {
  if (typeof _profile === 'undefined') {
    _profile = decodeJWT($Token.getIDToken());

    // this will be executed only once, when id_token is loaded from localStorage
    if (typeof _profile !== 'undefined') {
      // keep a ref to _profile.exp time
      _profile_exp = _profile.exp || undefined;

      // remove all protocol claims from profile
      var filter_out_protocol_claims = ['iss', 'sub', 'aud', 'exp', 'iat', 'nbf',
                                        'auth_time', 'nonce', 'acr', 'amr', 'azp',
                                        'jti', 'at_hash', 'idp',
                                        '_claim_names', '_claim_sources'];
      filter_out_protocol_claims.forEach(function(key){
        delete _profile[key];
      });
    }
  }

  // this will be executed each time func is called and _profile is set
  if (typeof _profile !== 'undefined') {
    // For more information on the `+new`, check at
    // http://stackoverflow.com/questions/9430357/please-explain-why-and-how-new-date-works-as-workaround-for-date-now-in
    //
    // check if id_token is expired, and signout the user if true
    var current_time_since_epoch = Math.floor(+new Date() / 1000);
    if (typeof _profile_exp === 'undefined' || current_time_since_epoch >= _profile_exp) {

      // revoke token
      this.signOut();

      // send a revoke postMessage to opFrame
      sessionRevokeAccess();

      return;
    }
  }

  return _profile;
};

// send a revoke postMessage to opFrame
var sessionRevokeAccess = function() {
  if (typeof $aq.Session !== 'undefined') {
    $aq.Session.revokeAccess.apply($aq.Session);
  }
};

// public API
function AuthentiqProvider(opt) {
  var opts = opt || {};

  // keep reference to authentiq namespace
  $aq = window.authentiq;

  // keep reference to _storage default config
  $storage_defaults = $aq._defaults._storage;

  $Token = $aq.Token;
  $Event = $aq.Event;

  // check if we have an OP redirect fragment
  checkToken.call(this);

  // update HTML entities that are binded to form updates
  $Event('profile').subscribe([this.updateBindedElements, this]);

  // trigger profile update
  this.profile();
}

/**
* Verifies if the `user` is authenticated or not based on the `token`.
*
* @return {boolean}
*/
AuthentiqProvider.prototype.isSignedIn = function() {
  // server-side code can set user as signed in using `data-signed-in`
  var is_server_side_signed_in = $aq._defaults.provider.signed_in || false;

  return is_server_side_signed_in || !!loadProfile.apply(this);
};

/**
* Decodes the `id_token` and returns the profile if set.
* Also, it emits the `aq:profile` event.
*
* @return {object} or {undefined}
*/
AuthentiqProvider.prototype.profile = function() {

  var profile = loadProfile.apply(this);

  if (typeof profile !== 'undefined') {

    // publish event
    Utils.addEventInHistory('profile', profile);
  }

  return profile;
};

/**
* Updates all the binded elements in HTML DOM
*/
AuthentiqProvider.prototype.updateBindedElements = function() {
  var els, i, claim,
      profile = loadProfile.apply(this);

  els = document.querySelectorAll('[data-authentiq-profile-bind]');
  for (i = 0; i < els.length; ++i) {
    claim = els[i].dataset.authentiqProfileBind || '';
    if (profile[claim]) {
      if (typeof els[i].value !== 'undefined') {
        els[i].value = profile[claim];
      } else {
        els[i].innerHTML = profile[claim];
      }
    }
  }
};

/**
 * Revokes the `token` and removes the stored `token` from localStorage.
 * Also, it emits the `aq:concluded` event.
 */
AuthentiqProvider.prototype.signOut = function(e) {

  // if RP has initiated the signout then let the OP update its status
  if (typeof e !== 'undefined') {
    e.preventDefault();

    // send a revoke postMessage to opFrame
    sessionRevokeAccess();
  }

  // remove token cookie
  $Token.destroy();

  _profile = undefined;

  // publish event
  $Event('concluded').broadcast();
};

// this exists only for BDD tests
if (BDD_TESTS) {
  // for testing the fragment parsing
  AuthentiqProvider.prototype.checkToken = function(e) {
    checkToken.call(this);
  };
}

module.exports = AuthentiqProvider;
