// session.js
// ------------------

// script strict mode syntax
'use strict';

// private vars and functions
var Utils = require('./utils');

// Shorthand
var $aq;
var $provider_defaults;
var $Token;

if (DEBUG) {
  var $log = Utils.log;
}

// TODO:
// Upon receipt of changed, the RP MUST perform re-authentication with prompt=none to obtain the current session state at the OP. Upon receipt of error, the RP MUST NOT perform re-authentication with prompt=none, so as to not cause potential infinite loops that generate network traffic to the OP.

// When the RP detects a session state change, it SHOULD first try a prompt=none request within an iframe to obtain a new ID Token and session state, sending the old ID Token as the id_token_hint. If the RP receives an ID token for the same End-User, it SHOULD simply update the value of the session state. If it doesn't receive an ID token or receives an ID token for another End-User, then it needs to handle this case as a logout for the original End-User.
// Note that the session state is origin bound. Session state SHOULD be returned upon an authentication failure.

// the provider frame
var _target_origin;
var _available_commands = ['concluded'];
var _op_frame;

// public API
function AuthentiqSession() {

  // keep reference to authentiq namespace
  $aq = window.authentiq;

  this.init();

  // on signed_out event prepare the Session module for a new session
  $aq.Event('concluded').subscribe([this.destroy, this]);
}

AuthentiqSession.prototype.init = function() {
  // if already signed-in then start iFrame
  if ($aq.Provider.isSignedIn() && !!$aq.Token.getSessionState()) {
    // unsubscribe for handling iFrame init on sign in
    $aq.Event('profile').unsubscribe(this.init);

    // keep reference to provider default config
    $provider_defaults = $aq._defaults.provider;

    $Token = $aq.Token;

    // extract provider_url origin so as we can verify iframe postMessage origin
    // that's useful for development branches
    var temp_link = document.createElement('a');
    temp_link.setAttribute('href', $provider_defaults.provider_uri);

    _target_origin = temp_link.origin;

    _op_frame = this.renderOPFrame().contentWindow;
    Utils.on(window, 'message', this.receiveMessage, this);

  // wait for the user to sign-in first
  } else {
    $aq.Event('profile').unsubscribe(this.init).subscribe([this.init, this]);
  }
};

AuthentiqSession.prototype.destroy = function() {
  Utils.off(window, 'message', this.receiveMessage, this);

  this.init();
};

AuthentiqSession.prototype.renderOPFrame = function() {
  var iframe, existing_op_frame,
      postMessage = this.postMessage;

  $log('timeEnd', 'initFrame');
  $log('timeEnd', '_initFrame');

  // if iFrame exists already then use this one
  existing_op_frame = document.querySelector('.opframe');
  if (existing_op_frame) {
    // force reload of opFrame so as we can bind the new session
    existing_op_frame.src = $provider_defaults.provider_uri + $provider_defaults.check_session_iframe;

    return existing_op_frame;
  }

  iframe = document.createElement('iframe');
  iframe.setAttribute('frameBorder', '0');
  iframe.style.display = 'none';

  iframe.onload = function() {
    // set the contentWindow to opFrame
    _op_frame = this.contentWindow;

    // check status with opFrame
    postMessage();
  };

  iframe.className = 'opframe';
  iframe.src = $provider_defaults.provider_uri + $provider_defaults.check_session_iframe;

  try {
    document.body.appendChild(iframe);
  }
  catch (e) {
    Utils.on(document, 'DOMContentLoaded', function() {
      (document.body || document.documentElement).appendChild(iframe);
    });
  }

  $log('timeEnd', 'renderFrame');
  $log('timeEnd', '_renderFrame');

  return iframe;
};

// sends a revoke postMessage to opFrame
AuthentiqSession.prototype.revokeAccess = function() {
  this.postMessage('concluded');
};

AuthentiqSession.prototype.postMessage = function(cmd) {
  $log('timeEnd', 'rpFrame');

  // if not signed-in then skip
  if (!$aq.Provider.isSignedIn()) {
    return;
  }

  // send a check session msg
  var msg = $provider_defaults.client_id + ' ' + $Token.getSessionState();

  // if cmd is set then append a cmd to postMessage
  if (typeof cmd !== 'undefined' && _available_commands.indexOf(cmd) > -1) {
    msg += ' ' + cmd;
  }

  $log('warn', 'RP: Sending message to ' + _target_origin + ': ' + msg);

  try {
    _op_frame.postMessage(msg, _target_origin);
  }
  catch(ex) {
     $log('warn', 'RP: postMessage failed to be send');
  }
};

AuthentiqSession.prototype.receiveMessage = function(e) {
  var stat;

  // do not accept message from an unexpected origin or frame
  if (e.origin !== _target_origin || e.source != _op_frame) {
    $log('warn', 'RP: Invalid origin ' + e.origin);
    return;
  }

  $log('timeEnd', 'opFrame');
  $log('timeEnd', '_iFrame');

  $log('warn', 'RP: Received msg from ' + e.origin + ': ' + e.data);

  stat = e.data;

  // Authentiq
  if (stat === 'changed') {
      $log('warn', 'RP: Received changed state');
      // var iframe_src = targetOrigin + '/authorize/?prompt=none&id_token_hint=' + $Token.getIDToken();
      // this.renderFrame(iframe_src);

      // TODO: how can we update the token?

  } else if (stat === 'concluded') {
      $log('warn', 'RP: Received logout state');
      $aq.Provider.signOut();
  }
};

module.exports = AuthentiqSession;
