  // }

  // var encodedProfile = data.token.split('.')[1];
  //         var profile = JSON.parse(url_base64_decode(encodedProfile));

  decodeJWT: function(token) {
    var segments = token ? token.split('.') : [];

    if (segments.length !== 3) {
      // prepend `aq:` in event name, as we use the radio class directly, instead of authentiq.Event
      radio('aq:error').broadcast({
        error: 'client_error',
        error_description: 'Failed to decode ID Token.'
      });

      this.log('Not enough or too many segments');

      return;
    }

    // All segment should be base64
    var headerSeg = segments[0];
    var payloadSeg = segments[1];
    var signatureSeg = segments[2];

    // base64 decode and parse JSON
    var header = JSON.parse(base64urlDecode(headerSeg)) || undefined;
    var payload = JSON.parse(base64urlDecode(payloadSeg)) || undefined;

    return {
      header: header,
      payload: payload,
      signature: signatureSeg
    };
  },

