// button.js
// ------------------

// script strict mode syntax
'use strict';

var Utils = require('./utils');

// Shorthand
var $aq;
var $defaults;
var $storage_defaults;
var $provider_defaults;
var $API;
var $Provider;

if (DEBUG) {
  var $log = Utils.log;
}

var styles_appended_in_DOM = false;

var svgLogo = function(theme) {
  // keep width/height attrs as they solve scaling issues at IE9/10
  var svg = '<svg xmlns=\'http://www.w3.org/2000/svg\' viewBox=\'0 0 30 27\' width=\'30\' height=\'27\'>' +
    '<path id=\'aq-logo-bg\' style=\'fill:'+ theme[3] +'\' d=\'M30 18.4c0 .7-.4 1.4-.8 1.7l-13.6 7H14L.8 20c-.4 0-.8-1-.8-1.5v-10C0 8 .4 7.3.8 7L14 0c.4-.3 1.2-.3 1.6 0l13.6 7c.4.2.8 1 .8 1.6v9.8z\'/>' +
    '<path id=\'aq-logo-a\' style=\'fill:'+ theme[4] +'\' d=\'M11 13.7c0-1.8 1.3-3.3 3-3.3s3 1.5 3 3.3c0 1.8-1.3 3.2-3 3.2s-3-1.7-3-3.5zm11.5 3H22c-.4 0-1-.2-1-.4-.3 0-.5-.4-.5-.8V7.7h-3.2V9c-.5-.5-1-.8-1.7-1-.6-.3-1.5-.5-2.3-.5-1 0-1.7.2-2.4.5-1 .3-1.6.8-2 1.3-.6.6-1 1.2-1 2-.6.7-.7 1.5-.7 2.3 0 1 0 1.7.4 2.4.2.7.6 1.4 1 2 .6.5 1.2 1 2 1.3.6.3 1.4.5 2.3.5 1 0 1.8-.2 2.5-.6.7-.3 1.2-.8 1.7-1.5.3.6.7 1 1.3 1.5.5.4 1 .6 1.8.6.5 0 1 0 1.5-.2l.5-.2v-2.8z\'/>' +
  '</svg>';

  // Encode <, >, #, any remaining " and other known-unsafe-in-URLs characters
  // tip: this fixes firefox
  return svg.replace(/"/g,'\'')
            .replace(/</g,'%3C')
            .replace(/>/g,'%3E')
            .replace(/&/g,'%26')
            .replace(/#/g,'%23')
            .replace(/\s+/g,' ');
};

var getCssStyles = function() {
  // button themes
  var themes = {
    // name: [bg, text, hover-bg, primary, secondary]
    // name: [bg, text, hover-bg, logo-bg, logo-char]
    basic: ['#895fc0', '#fff', '#6a3fa1', '#fff', '#f05023'],
    orange: ['#f05023', '#fff', '#cd380e', '#fff', '#f05023'],
    grey: ['#afb6bb', '#333', '#919aa1', '#fff', '#f05023'],
    white: ['#fff', '#444', '#d4d4d4', '#f05023', '#fff']
  }, css = '', theme;

  // add basic button styles
  css +=  '.authentiq-button-style{' +
            'padding:8px 8px 8px 46px;' +
            'min-height:40px;' +
            'background-color:' + themes.basic[0] + ';' +
            'background-image:url("data:image/svg+xml;charset=utf8,'+ svgLogo(themes.basic) +'") !important;' +
            'background-size: 31px 28px !important;' +
            'background-position: 8px center !important;' +
            'background-repeat: no-repeat !important;' +
            'color:' + themes.basic[1] + ';' +
            'display:inline-block;' +
            'border:0 none;' +
            'border-radius:5px;' +
            'font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;font-weight:600;' +
            'letter-spacing:.016em' +
          '}' +

          '.authentiq-button-style:hover,' +
          '.authentiq-button-style:focus{' +
          // '.authentiq-button-style:active{' +
            'background-color:' + themes.basic[2] + ';' +
            'color:' + themes.basic[1] +
          '}';

  // add theme styles
  for (theme in themes) {

    // skip default theme
    if (theme == 'basic') continue;

    css +=  '.authentiq-button-style.' + theme + '-theme{' +
              'background-color:' + themes[theme][0] +';' +
              'color:' + themes[theme][1] + ';' +

              // add another SVG image only if needed
              // so check if basic SVG logo is different from this theme one
              (
                (themes.basic[3] !== themes[theme][3] || themes.basic[4] !== themes[theme][4]) ? 'background-image: url("data:image/svg+xml;charset=utf8,' + svgLogo(themes[theme]) + '") !important;' : ''
              ) +
            '}' +

            '.authentiq-button-style.' + theme + '-theme:hover,' +
            '.authentiq-button-style.' + theme + '-theme:focus{' +
              'background-color:' + themes[theme][2] + ';' +
              'color:' + themes[theme][1] +
            '}';
  }

  return css;
};

// subscribed events callbacks
var signedIn = function(profile) {
  profile = profile || {};

  var claims = this.options.sign_out_claim.split(' '), claim,
      text = this.options.sign_out_text;
  for (claim in claims) {
    if (profile[claims[claim]]) {
      text += ' ' + profile[claims[claim]];
      break;
    }
  }

  // update text for signed in user
  this.el.innerHTML = text;

  // unbind pre-bind events from button
  Utils.off(this.el, 'click', $Provider.signOut, this);
  Utils.off(this.el, 'click', $API.signIn, this);

  // bind click event
  Utils.on(this.el, 'click', $Provider.signOut, this);
};

var signedOut = function() {
  // update text for signed in user
  this.el.innerHTML = this.options.sign_in_text;

  // unbind pre-bind events from button
  Utils.off(this.el, 'click', $Provider.signOut, this);
  Utils.off(this.el, 'click', $API.signIn, this);

  // bind click event
  Utils.on(this.el, 'click', $API.signIn, this);
};

var eventsSubscribe = function() {
  $aq.Event('profile').subscribe([signedIn, this]);
  $aq.Event('concluded').subscribe([signedOut, this]);
};

// var AuthentiqButton = (function() {
function AuthentiqButton(el, opt) {
  var attr, attr_name, hash_key,
      opts = opt || {};

  // check if element is already initialized
  if (el !== 'undefined' && el.hasAttribute('data-authentiq-button')) {
    return;
  }

  // create a random button id
  this.id = Math.floor(Math.random() * (9999 - 1000 + 1)) + 1000;

  // keep reference to authentiq namespace
  $aq = window.authentiq;

  // keep reference to _storage default config
  $defaults = $aq._defaults.button;
  $storage_defaults = $aq._defaults._storage;
  $provider_defaults = $aq._defaults.provider;

  $API = $aq._API;
  $Provider = $aq.Provider;

  // handy refs to API functions which uses the context `this` from the button
  this.authorizeURL = $API.authorizeURL;
  this.signIn = $API.signIn;

  if (!styles_appended_in_DOM) {
    styles_appended_in_DOM = true;

    // add CSS styles, a style tag exists
    var style_tag = document.querySelector('.authentiq-styles');
    if (style_tag) {
      style_tag.innerText += getCssStyles();
    }
  }

  // subscribe to events
  eventsSubscribe.apply(this);

  // merge element options with button defaults
  this.options = Object.assign({}, $defaults, opts);

  // read only the public options
  for (attr in this.options) {
    attr_name = 'data-' + attr.split('_').join('-');
    if (el.hasAttribute(attr_name)) {
      this.options[attr] = el.getAttribute(attr_name);
    }
  }

  // get signed in text from button if is same as default
  if (this.options.sign_in_text === $defaults.sign_in_text && el.tagName.toLowerCase() !== 'script' && el.innerHTML.trim() !== '') {
    this.options.sign_in_text = el.innerHTML.trim();
  }

  var hash_keys = ['state', 'nonce'];
  for (hash_key in hash_keys) {
    if (this.options[hash_keys[hash_key]] === '') {
      this.options[hash_keys[hash_key]] = Utils.generateNonce(8) || '';
    }
  }

  // always append openid scope if it doesn't exist in scopes
  if (this.options.scope !== '' && this.options.scope.indexOf('openid') === -1) {
    this.options.scope += ' openid';
  }

  if (this.options.redirect_uri === '') {

    // when redirect_uri is not set, snippet only accepts response_mode=fragment
    this.options.response_mode = 'fragment';

    this.options.redirect_uri = window.location.href.split('#')[0];

  // handle relative redirect_uris
  } else if (!/^https?:\/\//i.test(this.options.redirect_uri)) {
    var delim = this.options.redirect_uri[0] !== '/' ? '/' : '';
    this.options.redirect_uri = window.location.origin + delim + this.options.redirect_uri;
  }

  if (el.tagName.toLowerCase() === 'script') {
    this.scriptTag = el;
    this.renderButton();
  } else {
    this.el = el;
  }

  // add class to button for styling purposes
  if (this.el.className.indexOf('authentiq-button') === -1) {
    this.el.className += ' authentiq-button';
  }

  // add theme support
  if (this.options.theme && this.options.theme !== 'none') {
    this.el.className += ' authentiq-button-style ' + this.options.theme + '-theme';
  }

  // initialize the button

  // mark the element as initiated, with a unique id
  this.el.setAttribute('data-authentiq-button', this.id);

  // update text and bind events
  if ($Provider.isSignedIn()) {
    signedIn.apply(this);
  } else {
    signedOut.apply(this);
  }

  // later unsubscribe events when buttons are removed
  //
  // DOMNodeRemoved DOMNodeRemovedFromDocument
  // Utils.on(this.el, 'DOMNodeRemovedFromDocument', function(event) {
  //   // console.log('*************************', event);
  //   $aq.Event('profile').unsubscribe(signedIn);
  //   $aq.Event('concluded').unsubscribe(signedOut);
  // });
}

// Render the button
AuthentiqButton.prototype.renderButton = function() {

  // Create wrapper div and apply styles/class
  this.el = document.createElement('button');

  this.scriptTag.parentNode.insertBefore(this.el, this.scriptTag);
};

module.exports = AuthentiqButton;
