// token.js
// ------------------

// script strict mode syntax
'use strict';

// var Cookies = require('cookies-js');
var Utils = require('./utils');

// Shorthand
var $aq;
var $defaults;

if (DEBUG) {
  var $log = Utils.log;
}

function AuthentiqToken() {

  // keep reference to authentiq namespace
  $aq = window.authentiq;

  // keep reference to _storage default config
  $defaults = $aq._defaults._storage;
}

/**
 * Set IDToken
 */
AuthentiqToken.prototype.setIDToken = function(id_token) {
  localStorage.setItem($defaults.id_token, id_token);
};

/**
 * Get IDToken
 */
AuthentiqToken.prototype.getIDToken = function() {
  return localStorage.getItem($defaults.id_token) || undefined;
};

/**
 * Set Session State
 */
AuthentiqToken.prototype.setSessionState = function(session_state) {
  localStorage.setItem($defaults.session_state, session_state);
};

/**
 * Get Session State
 */
AuthentiqToken.prototype.getSessionState = function() {
  return localStorage.getItem($defaults.session_state) || undefined;
};

/**
 * Remove token
 */
AuthentiqToken.prototype.destroy = function() {
  localStorage.removeItem($defaults.id_token);
  localStorage.removeItem($defaults.session_state);
};

module.exports = AuthentiqToken;
