// ES5 [].indexOf
if (!Array.prototype.indexOf) {
  Array.prototype.indexOf = function(s) {

    for (var j = 0; j < this.length; j++) {
      if (this[j] === s) {
        return j;
      }
    }

    return -1;
  };
}

