// api.js
// ------------------

// script strict mode syntax
'use strict';

var queryString = require('query-string');
var Utils = require('./utils');

// Shorthand
var $aq;
var $storage_defaults;
var $provider_defaults;

if (DEBUG) {
  var $log = Utils.log;
}

var _iframe;

var authorize_args = {
  required_opts: ['response_type', 'response_mode', 'client_id', 'redirect_uri', 'state'],

  // aditional optional values are: max_age, id_token_hint, login_hint, acr_values
  optional_opts: ['scope', 'nonce', 'display', 'prompt', 'ui_locales', 'display_errors']
};

var styles_appended_in_DOM = false;
var pre_existing_iframe = false;

var getCssStyles = function() {
  // add modal styles
  return  '#authentiq-session-frame{' +
            'position:fixed;' +
            'left:0;' +
            'top:0;' +
            'width:100%;' +
            'height:100%;' +
            'z-index:9998;' +
          '}' +

          // in iOS stops scrolling of the parent window of RP
          '.aq-noscroll{' +
            'position: fixed !important;' +
            'overflow: hidden !important;' +
            'left: 0;' +
            'right: 0;' +
          '}';
};

// http://stackoverflow.com/a/4682246/543431
var centerWindowCoordiantes = function(width, height) {
  var a = typeof window.screenX != 'undefined' ? window.screenX : window.screenLeft;
  var i = typeof window.screenY != 'undefined' ? window.screenY : window.screenTop;
  var g = typeof window.outerWidth != 'undefined' ? window.outerWidth : document.documentElement.clientWidth;
  var f = typeof window.outerHeight != 'undefined' ? window.outerHeight: (document.documentElement.clientHeight - 22);
  var h = (a < 0) ? window.screen.width + a : a;
  var left = parseInt(h + ((g - width) / 2), 10);
  var top = parseInt(i + ((f - height) / 2.5), 10);

  return 'width=' + width +
        ',height=' + height +
        ',left=' + left +
        ',top=' + top + ',';
};

var removeModal = function() {
  if (typeof _iframe !== 'undefined') {
    _iframe.parentNode.removeChild(_iframe);
  }

  // toggle the no-scroll class on the body
  Utils.removeClass(document.body, 'aq-noscroll');

  _iframe = undefined;
};

function AuthentiqAPI(opt) {
  var hash_key,
      opts = opt || {};

  // keep reference to authentiq namespace
  $aq = window.authentiq;

  // keep reference to _storage default config
  $storage_defaults = $aq._defaults._storage;
  $provider_defaults = $aq._defaults.provider;

  // merge element options with button defaults
  this.options = Object.assign({}, $aq._defaults.button, opts);

  if (document.getElementById('authentiq-session-frame')) {
    pre_existing_iframe = true;
  }

  // if dev handles the iframe on his own skip styles and event bindings
  if (!pre_existing_iframe) {

    // load styles only once
    if (!styles_appended_in_DOM) {
      styles_appended_in_DOM = true;

      // load CSS style
      var style = document.createElement('style');
      style.className = 'authentiq-styles';
      style.innerHTML = getCssStyles();
      document.getElementsByTagName('head')[0].appendChild(style);
    }

    // handle closing of modal when escape key is pressed
    // Utils.on(window, 'keydown', function (event) {
    //   if (event.keyCode === 27) {
    //     removeModal();
    //   }
    // });
  }

  var hash_keys = ['state', 'nonce'];
  for (hash_key in hash_keys) {
    if (typeof this.options[hash_keys[hash_key]] === 'undefined' || this.options[hash_keys[hash_key]] === '') {
      this.options[hash_keys[hash_key]] = Utils.generateNonce(8) || '';
    }
  }

  // always append openid scope if it doesn't exist in scopes
  if (typeof this.options.scope !== 'undefined' &&
      this.options.scope !== '' && this.options.scope.indexOf('openid') === -1)
  {
    this.options.scope += ' openid';
  }

  if (typeof this.options.redirect_uri === 'undefined' || this.options.redirect_uri === '') {

    // when redirect_uri is not set, snippet only accepts response_mode=fragment
    this.options.response_mode = 'fragment';

    this.options.redirect_uri = window.location.href.split('#')[0];

  // handle relative redirect_uris
  } else if (!/^https?:\/\//i.test(this.options.redirect_uri)) {
    var delim = this.options.redirect_uri[0] !== '/' ? '/' : '';
    this.options.redirect_uri = window.location.origin + delim + this.options.redirect_uri;
  }
}

AuthentiqAPI.prototype.authorizeURL = function(opt) {
  var params = {}, opts = opt || {}, options, key,
      option_keys = authorize_args.required_opts.concat(authorize_args.optional_opts);

  // merge passed options with instance defaults
  options = Object.assign({}, this.options, opts);

  // add vars for querystring
  for (key in option_keys) {

    // if is optional param and it has no value then don't add it
    if (authorize_args.optional_opts.indexOf(option_keys[key]) > -1 && options[option_keys[key]] === '') {
      continue;
    }

    //   params.push(encodeURIComponent(option_keys[key]) + "=" + encodeURIComponent(this.options[option_keys[key]]));
    params[option_keys[key]] = options[option_keys[key]] || $provider_defaults[option_keys[key]];
  }

  // return this.options.provider_uri + "?" + params.join("&");
  return $provider_defaults.provider_uri + $provider_defaults.authorize_path + "?" + queryString.stringify(params);
};

AuthentiqAPI.prototype.signIn = function(event) {
  if (typeof event !== 'undefined') {
    event.preventDefault();
  }

  // set state cookie
  localStorage.setItem($storage_defaults.state, this.options.state);

  // set nonce cookie
  localStorage.setItem($storage_defaults.nonce, this.options.nonce);

  if (this.options.display === 'page') {
    window.location.href = this.authorizeURL();

  } else if (this.options.display === 'popup') {

    // open popup or bring to focus
    if (typeof $aq.popup_window === 'undefined' || $aq.popup_window.closed) {
      // handle popup callback
      $aq.handlePopupCallback = function(event_history) {

        $aq.handlePopupCallback = undefined;
        $aq.popup_window = undefined;

        Utils.setEventHistory(event_history);

        // fire all events that happened before DOMContentLoaded
        Utils.fireEventsFromHistory();
      };

      $aq.popup_window = window.open(
          this.authorizeURL(),
          'Authentiq Connect',
           centerWindowCoordiantes(450, 700) + 'scrollbars=1,location=0,toolbar=0,status=0,menubar=0,personalbar=0,resizable=0,dependent=1,dialog=0,close=1,modal=1'
      );
    }
    else {
      $aq.popup_window.focus();
    }

  } else {
    // handle modal callback
    $aq.handleModalCallback = function(event_history) {
      removeModal();

      $aq.handleModalCallback = undefined;

      Utils.setEventHistory(event_history);

      // fire all events that happened before DOMContentLoaded
      Utils.fireEventsFromHistory();
    };

    // toggle the no-scroll class on the body
    Utils.addClass(document.body, 'aq-noscroll');

    if (pre_existing_iframe) {
      var iframe = document.getElementById('authentiq-session-frame');

      iframe.src = this.authorizeURL();

      // on some mobile browser when the overlay was previously
      // opened and scrolled, if you open it again it doesn't
      // reset its scrollTop property
      iframe.scrollTop = 0;

    } else {
      _iframe = document.createElement('iframe');
      _iframe.setAttribute('frameBorder', '0');
      _iframe.id = 'authentiq-session-frame';
      _iframe.src = this.authorizeURL();

      _iframe.onload = function() {
        // set focus to iFrame, so as OP can handle JS events
        _iframe.contentWindow.focus();
      };

      document.body.appendChild(_iframe);
    }
  }
};

module.exports = AuthentiqAPI;
